body {
  padding: 0;
  margin: 0;
  font-family: 'Raleway', sans-serif;
}

a {
  text-decoration: none;
  color: #000;

  &:hover {
    text-decoration: underline;
  }
}

header {
  border-bottom: 2px solid #ccc;
  padding: 16px 30px;
}

.navContainer {
  display: flex;
  justify-content: space-around;
  align-items: center;
  max-width: 960px;
  margin: 0 auto;

  nav {
    display: flex;
    gap: 16px;
  }
}

.container {
  width: 100%;
  margin-right: auto;
  margin-left: auto;
}